function FAQItem({ q, a, icon, index }: { q: string, a: string, icon: string, index: number }) {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5, delay: index * 0.05 }}
      viewport={{ once: true }}
      className="glass rounded-xl border border-white/10 hover:border-blue-500/30 transition-all duration-300 overflow-hidden"
    >
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="w-full p-6 flex items-center justify-between gap-4 text-left hover:bg-white/5 transition-colors duration-200"
      >
        <div className="flex items-center gap-4 flex-1">
          <span className="text-2xl flex-shrink-0">{icon}</span>
          <h3 className="text-lg font-bold text-white">{q}</h3>
        </div>
        <svg
          className={`w-6 h-6 text-blue-400 flex-shrink-0 transition-transform duration-300 ${isOpen ? 'rotate-180' : ''
            }`}
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          strokeWidth={2}
        >
          <path strokeLinecap="round" strokeLinejoin="round" d="M19 9l-7 7-7-7" />
        </svg>
      </button>

      <motion.div
        initial={false}
        animate={{
          height: isOpen ? 'auto' : 0,
          opacity: isOpen ? 1 : 0
        }}
        transition={{ duration: 0.3, ease: 'easeInOut' }}
        className="overflow-hidden"
      >
        <div className="px-6 pb-6 pl-16">
          <p className="text-gray-400 leading-relaxed">{a}</p>
        </div>
      </motion.div>
    </motion.div>
  );
}

