"use client";

import { useState } from "react";
import { motion } from "framer-motion";

export function ROICalculator() {
    const [hoursPerWeek, setHoursPerWeek] = useState(10);
    const [hourlyRate, setHourlyRate] = useState(50);

    const weeklysavings = hoursPerWeek * hourlyRate;
    const monthlySavings = weeklysavings * 4;
    const yearlySavings = monthlySavings * 12;

    return (
        <div className="glass p-8 rounded-xl border border-blue-500/20 bg-gradient-to-br from-blue-500/5 to-indigo-500/5">
            <h3 className="text-2xl font-bold text-white mb-6 text-center">
                Calculate Your Potential Savings
            </h3>

            <div className="space-y-6 mb-8">
                {/* Hours per week slider */}
                <div>
                    <label className="flex justify-between text-sm text-gray-400 mb-2">
                        <span>Hours spent on repetitive tasks per week:</span>
                        <span className="text-white font-semibold">{hoursPerWeek} hours</span>
                    </label>
                    <input
                        type="range"
                        min="1"
                        max="40"
                        value={hoursPerWeek}
                        onChange={(e) => setHoursPerWeek(Number(e.target.value))}
                        className="w-full h-2 bg-white/10 rounded-lg appearance-none cursor-pointer accent-blue-500"
                    />
                </div>

                {/* Hourly rate slider */}
                <div>
                    <label className="flex justify-between text-sm text-gray-400 mb-2">
                        <span>Average hourly cost of your team:</span>
                        <span className="text-white font-semibold">${hourlyRate}/hour</span>
                    </label>
                    <input
                        type="range"
                        min="20"
                        max="200"
                        step="10"
                        value={hourlyRate}
                        onChange={(e) => setHourlyRate(Number(e.target.value))}
                        className="w-full h-2 bg-white/10 rounded-lg appearance-none cursor-pointer accent-blue-500"
                    />
                </div>
            </div>

            {/* Results */}
            <motion.div
                key={`${hoursPerWeek}-${hourlyRate}`}
                initial={{ scale: 0.95, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ duration: 0.3 }}
                className="bg-gradient-to-r from-blue-600 to-indigo-600 p-6 rounded-xl text-center"
            >
                <div className="text-sm text-blue-100 mb-2">You could save:</div>
                <div className="text-4xl md:text-5xl font-bold text-white mb-4">
                    ${monthlySavings.toLocaleString()}<span className="text-2xl">/month</span>
                </div>
                <div className="grid grid-cols-2 gap-4 text-sm">
                    <div className="bg-white/10 rounded-lg p-3">
                        <div className="text-blue-100">Weekly</div>
                        <div className="text-xl font-bold text-white">${weeklysavings.toLocaleString()}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3">
                        <div className="text-blue-100">Yearly</div>
                        <div className="text-xl font-bold text-white">${yearlySavings.toLocaleString()}</div>
                    </div>
                </div>
            </motion.div>

            <p className="text-center text-sm text-gray-400 mt-6">
                Based on automating {hoursPerWeek} hours/week at ${hourlyRate}/hour
            </p>
        </div>
    );
}
